import { useState, useEffect, useCallback, useRef } from 'react';
// @mui
import {
    Box,
    Card,
    Container,
    Typography,
    Stack,
    List,
    ListItem,
    ListItemText,
    Divider,
    TextField,
    Button,
    IconButton,
    Chip,
    Paper,
} from '@mui/material';
import SendIcon from '@mui/icons-material/Send';
import CloseIcon from '@mui/icons-material/Close';
// components
import Page from '../../components/Page';
import Iconify from '../../components/Iconify';
import axios from '../../utils/axios';

// ----------------------------------------------------------------------

export default function SupportManagement() {
    const [tickets, setTickets] = useState([]);
    const [selectedTicket, setSelectedTicket] = useState(null);
    const [messages, setMessages] = useState([]);
    const [reply, setReply] = useState('');
    const [userTyping, setUserTyping] = useState(false);
    const [typingTimer, setTypingTimer] = useState(null);
    const [attachment, setAttachment] = useState(null);
    const fileInputRef = useRef(null);
    const accessToken = window.localStorage.getItem('accessToken');

    const fetchTickets = useCallback(async () => {
        try {
            const response = await axios.get('/api/admin/support/open-tickets');
            if (response.data.status === 'success') {
                setTickets(response.data.tickets);
            }
        } catch (error) {
            console.error(error);
        }
    }, []);

    const fetchMessages = useCallback(async (ticketId) => {
        try {
            const response = await axios.get(`/api/support/chat/${ticketId}/messages/user`);
            if (response.data.status === 'success') {
                setMessages(response.data.messages);
                setUserTyping(response.data.typing_status === 'user');
            }
        } catch (error) {
            console.error(error);
        }
    }, []);

    useEffect(() => {
        fetchTickets();
        const interval = setInterval(fetchTickets, 10000);
        return () => clearInterval(interval);
    }, [fetchTickets]);

    useEffect(() => {
        if (selectedTicket) {
            fetchMessages(selectedTicket.id);
            const interval = setInterval(() => fetchMessages(selectedTicket.id), 5000);
            return () => clearInterval(interval);
        }
    }, [selectedTicket, fetchMessages]);

    const handleTyping = (e) => {
        const val = e.target.value;
        setReply(val);
        if (!selectedTicket) return;

        // Simple debounce for typing status
        axios.post('/api/support/typing', { ticket_id: selectedTicket.id, status: 'start' });

        if (typingTimer) clearTimeout(typingTimer);
        const newTimer = setTimeout(() => {
            axios.post('/api/support/typing', { ticket_id: selectedTicket.id, status: 'stop' });
        }, 2000);
        setTypingTimer(newTimer);
    };

    const handleFileChange = (e) => {
        if (e.target.files && e.target.files[0]) {
            setAttachment(e.target.files[0]);
        }
    };

    const handleSendReply = async () => {
        if (!reply.trim() && !attachment) return; // Allow image only
        try {
            const formData = new FormData();
            formData.append('message', reply);
            if (attachment) {
                formData.append('image', attachment);
            }

            await axios.post(`/api/admin/support/chat/${selectedTicket.id}/reply`, formData, {
                headers: {
                    'Content-Type': 'multipart/form-data',
                }
            });
            setReply('');
            setAttachment(null);
            if (fileInputRef.current) fileInputRef.current.value = '';
            fetchMessages(selectedTicket.id);
        } catch (error) {
            console.error(error);
        }
    };

    const handleCloseTicket = async (ticketId) => {
        try {
            await axios.post(`/api/admin/support/ticket/${ticketId}/close`);
            setSelectedTicket(null);
            fetchTickets();
        } catch (error) {
            console.error(error);
        }
    };

    return (
        <Page title="Support Management">
            <Container maxWidth="xl">
                <Typography variant="h4" gutterBottom>
                    Support Center
                </Typography>

                <Stack direction="row" spacing={3} sx={{ height: '75vh' }}>
                    {/* Ticket List */}
                    <Card sx={{ width: 400, overflow: 'auto' }}>
                        <Box sx={{ p: 2, borderBottom: '1px solid #eee' }}>
                            <Typography variant="subtitle1">Active Sessions ({tickets.length})</Typography>
                        </Box>
                        <List>
                            {tickets.map((ticket) => (
                                <div key={ticket.id}>
                                    <ListItem
                                        button
                                        selected={selectedTicket?.id === ticket.id}
                                        onClick={() => setSelectedTicket(ticket)}
                                    >
                                        <ListItemText
                                            primary={`${ticket.user_fullname} (@${ticket.username})`}
                                            secondary={
                                                <Stack direction="row" spacing={1} alignItems="center" sx={{ mt: 0.5 }}>
                                                    <Chip
                                                        label={ticket.current_handler === 'ai' ? 'With Aboki AI' : 'Human Support'}
                                                        size="small"
                                                        variant="outlined"
                                                        color={ticket.current_handler === 'ai' ? 'info' : 'success'}
                                                        sx={{ height: 20, fontSize: '0.65rem' }}
                                                    />
                                                    <Typography variant="caption" noWrap sx={{ maxWidth: 100 }}>
                                                        {ticket.last_message || 'New Chat'}
                                                    </Typography>
                                                </Stack>
                                            }
                                            primaryTypographyProps={{ variant: 'subtitle2' }}
                                        />
                                        <Chip
                                            label={ticket.status}
                                            size="small"
                                            color={ticket.status === 'open' ? 'error' : 'warning'}
                                            sx={{ ml: 1 }}
                                        />
                                    </ListItem>
                                    <Divider />
                                </div>
                            ))}
                            {tickets.length === 0 && (
                                <Box sx={{ p: 3, textAlign: 'center' }}>
                                    <Typography variant="body2" color="text.secondary">
                                        No active support requests
                                    </Typography>
                                </Box>
                            )}
                        </List>
                    </Card>

                    {/* Chat View */}
                    <Card sx={{ flexGrow: 1, display: 'flex', flexDirection: 'column' }}>
                        {selectedTicket ? (
                            <>
                                <Box sx={{ p: 2, bgcolor: 'background.neutral', display: 'flex', justifyContent: 'space-between', alignItems: 'center', borderBottom: '1px solid #eee' }}>
                                    <Stack direction="row" spacing={2} alignItems="center">
                                        <Typography variant="subtitle1">
                                            Chat with {selectedTicket.user_fullname}
                                        </Typography>
                                        {selectedTicket.current_handler === 'ai' && (
                                            <Chip label="User speaking to AI" color="info" size="small" />
                                        )}
                                    </Stack>
                                    <Button
                                        variant="outlined"
                                        color="error"
                                        size="small"
                                        startIcon={<CloseIcon />}
                                        onClick={() => handleCloseTicket(selectedTicket.id)}
                                    >
                                        Close Session
                                    </Button>
                                </Box>

                                <Box sx={{ flexGrow: 1, p: 2, overflow: 'auto', bgcolor: '#F9FAFB' }}>
                                    <Stack spacing={2}>
                                        {messages.map((msg) => {
                                            const isAgent = msg.sender_type === 'agent';
                                            const isBot = msg.sender_type === 'bot';
                                            const isUser = msg.sender_type === 'user';

                                            return (
                                                <Box
                                                    key={msg.id}
                                                    sx={{
                                                        display: 'flex',
                                                        justifyContent: isUser || isBot ? 'flex-start' : 'flex-end',
                                                    }}
                                                >
                                                    <Paper
                                                        sx={{
                                                            p: 1.5,
                                                            maxWidth: '75%',
                                                            bgcolor: isUser ? 'white' : (isBot ? '#EDF2FF' : '#00A86B'),
                                                            color: isAgent ? 'white' : (isBot ? '#444' : 'text.primary'),
                                                            borderRadius: 2,
                                                            border: isUser ? '1px solid #E5E7EB' : 'none',
                                                            boxShadow: 1
                                                        }}
                                                    >
                                                        <Typography variant="caption" sx={{ fontWeight: 'bold', display: 'block', mb: 0.5, opacity: 0.8 }}>
                                                            {isUser ? 'User' : (isBot ? 'Aboki AI' : 'Agent (Me)')}
                                                        </Typography>
                                                        {msg.attachment_url && (
                                                            <Box
                                                                component="img"
                                                                src={msg.attachment_url}
                                                                alt="attachment"
                                                                sx={{ borderRadius: 1, mb: 1, maxHeight: 200, maxWidth: '100%', display: 'block' }}
                                                            />
                                                        )}
                                                        <Typography variant="body2">{msg.message}</Typography>
                                                        <Typography variant="caption" sx={{ opacity: 0.6, mt: 0.5, display: 'block', textAlign: 'right' }}>
                                                            {new Date(msg.created_at).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}
                                                        </Typography>
                                                    </Paper>
                                                </Box>
                                            );
                                        })}
                                    </Stack>
                                </Box>

                                <Box sx={{ p: 2, borderTop: '1px solid #eee' }}>
                                    {userTyping && (
                                        <Typography variant="caption" sx={{ ml: 1, mb: 1, fontStyle: 'italic', color: 'text.secondary', display: 'block' }}>
                                            User is typing...
                                        </Typography>
                                    )}
                                    <Stack direction="row" spacing={2} alignItems="center">
                                        <input
                                            type="file"
                                            hidden
                                            ref={fileInputRef}
                                            accept="image/*"
                                            onChange={handleFileChange}
                                        />
                                        <IconButton size="small" onClick={() => fileInputRef.current?.click()} color={attachment ? "success" : "default"}>
                                            <Iconify icon="eva:image-outline" />
                                        </IconButton>
                                        <TextField
                                            fullWidth
                                            size="small"
                                            placeholder={attachment ? "Image selected. Add caption..." : (selectedTicket.current_handler === 'ai' ? "Typing here will take over from AI..." : "Type your reply...")}
                                            value={reply}
                                            onChange={handleTyping}
                                            onKeyPress={(e) => {
                                                if (e.key === 'Enter') handleSendReply();
                                            }}
                                        />
                                        <IconButton color="primary" onClick={handleSendReply} sx={{ bgcolor: '#00A86B', color: 'white', '&:hover': { bgcolor: '#008f5a' } }}>
                                            <SendIcon />
                                        </IconButton>
                                    </Stack>
                                </Box>
                            </>
                        ) : (
                            <Box sx={{ flexGrow: 1, display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center' }}>
                                <Iconify icon="eva:message-square-outline" sx={{ fontSize: 60, color: 'text.disabled', mb: 2 }} />
                                <Typography color="text.secondary">Select an active support session to start</Typography>
                            </Box>
                        )}
                    </Card>
                </Stack>
            </Container>
        </Page>
    );
}
